@import './reset.css';

body {
    background: #fff;
}

.line {
    height: 16px;
    width: 100%;
    background: #f8f9fa;
    box-shadow: inset 0 8px 8px -8px rgba(0, 0, 0, 0.04), inset 0 -8px 8px -8px rgba(0, 0, 0, 0.04);
}

.black-nav {
    width: 100%;
    font-weight: 700;
    color: #333;
    padding: 20px;
}

.black-nav h4 {
    font-size: 20px;
    font-weight: 700;
}

.filter {
    width: 100%;
    display: block;
    text-align: end;
    padding: 20px 15px;
}

.filter .sort {
    padding: 10px;
    background: #96a5c9;
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    border-radius: 20px;
}

.filter .sort:hover {
    background: #7987a9;
    transition: all 0.3s ease-in;
}

.write {
    width: 88%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px;
    border: 1px solid #eee;
    border-radius: 30px;
    padding: 12px 15px;
    margin: 20px auto;
}

.write input {
    width: 85%;
    font-size: 15px;
}

.write input::placeholder {
    color: #888;
}

.write button {
    border: 1px solid #96a5c9;
    padding: 5px 10px;
    border-radius: 15px;
    color: #96a5c9;
    font-weight: 700;
}

div {
    box-sizing: border-box;
}

.list {
    padding: 20px 10px;
    border-bottom: 1px solid #f4f4f4;
}

.list h4 {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.list .title_right {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    font-weight: 400;
}

.list h4 img {
    width: 18px;
}
.modal {
    margin-top: 20px;
    padding: 30px 20px;
    background: #eee;
    text-align: left;
}

.modal h4 {
    font-size: 16px;
    margin-bottom: 20px;
}
.modal p {
    margin-bottom: 5px;
}
